<template>
    <div class="loginbox">
     <form action="LoginServlet" method="post" class="smart-green" id="form1">
                <h1>Please Login <span>Please fill all the texts in the fields then click the button.</span></h1>
                <label for="">
                    <span>User name:</span>
                    <input type="text" v-model="username" name="" id="username" placeholder="username">
                </label>

                <label for="">
                    <span>Keyword:</span>
                    <input type="password" name="" id="password" placeholder="密码">
                </label>

                <p class="logining">
                    <el-button id='log' plain  @click="open"> <router-link plain id="button" class="button" to="/office">登录</router-link></el-button>
                    <el-button id='register' type="submit" :plain="true" @click='prompt'><router-link  id="button" class="button" to="/register">注册</router-link></el-button>
                </p>

    
            </form>
            
            </div>
            
</template>

<script>
    export default{
        data(){
            return{
                username : "",
                aaa: ""
            }
        },
        methods:{
            asd(){
                console.log( this.username )
                this.aaa = this.username
            },
            open(){
                this.$notify({
                title:'注册成功',
                message:'恭喜您，加入了我们！',
                type:'success'
                });
            },
            errorinfo(){
                this.$message({
                message:'输入有误，请重新输入！',
                type:'woring'
                });
            },
            prompt(){
                this.$message({
                    showClose: true,
                    message:'请输入您的真实有效信息'
                })
            },
            
        }
    }
</script>

<style scoped>
#login{
    display: inline-block;
    line-height: 1;
    white-space: nowrap;
    cursor: pointer;
    background: #fff;
    border: 1px solid #dcdfe6;
    color: #606266;
    -webkit-appearance: none;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: 0;
    margin: 0;
    -webkit-transition: .1s;
    transition: all .1s;
    font-weight: 500;
 
    font-size: 14px;
    border-radius: 4px;
    background-color: #9DC45F;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-border-radius: 5px;
    border: none;
    padding: 10px 25px 10px 25px;
    color: #FFF;
    text-shadow: 1px 1px 1px #949494;
    -webkit-transition: all 0.5s;
}
#login:hover{
    background-color: #688934;
}
#log{
    padding: 0;
    border: 0;
}
#register{
    padding: 0;
    border: 0;
}
.logining {
    margin-top: 20px;
}
.smart-green {
    margin-left: auto;
    margin-right: auto;
    max-width: 366px;
    background: #F8F8F8;
    padding: 30px 30px 20px 30px;
    font: 12px Arial, Helvetica, sans-serif;
    color: #666;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    margin-top: 100px;
}

.smart-green h1 {
    font: 24px "Trebuchet MS", Arial, Helvetica, sans-serif;
    padding: 20px 0px 20px 40px;
    display: block;
    margin: -30px -30px 10px -30px;
    color: #FFF;
    background: #9DC45F;
    text-shadow: 1px 1px 1px #949494;
    border-radius: 5px 5px 0px 0px;
    -webkit-border-radius: 5px 5px 0px 0px;
    -moz-border-radius: 5px 5px 0px 0px;
    border-bottom: 1px solid #89AF4C;

}

.smart-green h1>span {
    display: block;
    font-size: 11px;
    color: #FFF;
}

.smart-green label {
    display: block;
    margin: 0px 0px 5px;
}

.smart-green label>span {
    float: left;
    margin-top: 10px;
    color: #5E5E5E;
}

.smart-green input[type="text"],
.smart-green input[type="password"],
.smart-green textarea,
.smart-green select {
    color: #555;
    height: 30px;
    line-height: 15px;
    width: 100%;
    padding: 0px 0px 0px 10px;
    margin-top: 2px;
    border: 1px solid #E5E5E5;
    background: #FBFBFB;
    outline: 0;
    -webkit-box-shadow: inset 1px 1px 2px rgba(238, 238, 238, 0.2);
    box-shadow: inset 1px 1px 2px rgba(238, 238, 238, 0.2);
    font: normal 14px/14px Arial, Helvetica, sans-serif;
}



.smart-green textarea {
    height: 100px;
    padding-top: 10px;
}

.smart-green select {
    /* background: url('down-arrow.png') no-repeat right, -moz-linear-gradient(top, #FBFBFB 0%, #E9E9E9 100%);
    background: url('down-arrow.png') no-repeat right, -webkit-gradient(linear, left top, left bottom, color-stop(0%, #FBFBFB), color-stop(100%, #E9E9E9)); */
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 0.01px;
    text-overflow: '';
    width: 100%;
    height: 30px;
}

.smart-green .button {
    background-color: #9DC45F;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-border-radius: 5px;
    border: none;
    padding: 10px 25px 10px 25px;
    color: #FFF;
    text-shadow: 1px 1px 1px #949494;
    transition: all 0.5s;
}

.smart-green .button:hover {
    background-color: #80A24A;
}
</style>



