<template>
<div>


    <el-tabs class='tableone' type='border-card'>
        <el-tab-pane label='早到榜'>
            <el-tag>王珍</el-tag>
            <el-tag type="success">张紫嫣</el-tag><br><br>
            <el-tag type="info">周婵</el-tag>
            <el-tag type="warning">周旭波</el-tag>
            <el-tag type="danger">唐宇航</el-tag>
        </el-tab-pane>
        <el-tab-pane label="迟到榜">
            <el-tag type="danger">王珍</el-tag>
            <el-tag type="danger">周婵</el-tag>
        </el-tab-pane>
        <el-tab-pane label='日历'>我在做什么</el-tab-pane>
        <el-tab-pane label='该弄啥'>不知道了</el-tab-pane>
    </el-tabs>
    
    
   <p class='work'>
       <small>北京时间：</small>{{time}}
    </p>
</div>
</template>
<script>
export default {
    name:'leftview',
    data() {
      return {
        isCollapse: true,
        startTime: '',
        endTime: '',
        time:''
      };
    },
    mounted:function() {
        var orderheight=document.body.clientHeight;
        document.getElementById('order-list').style.height=orderheight+'px';
        this.startTime();
        },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      startTime(){
        var today=new Date();
        var h=today.getHours();
        var m=today.getMinutes();
        var s=today.getSeconds();
        m=checkTime(m);
        s=checkTime(s);
        this.time=h + ":" +m +":"+ s;
        t=setTimeout('startTime()',500);
      },
      checkTime(i)
        {
        if (i<10) 
        {i="0" + i}
        return i
        }
    }
  }
</script>

<style>
/* .work {
    margin-top: 50px;
} */
.tableone {
        margin: 10px 0px 50px 0;
        height: 241px;
}
</style>



